.manfacturers {
  padding: rem(108px) 0 rem(29px);
  h5 {
    padding: rem(11px) 0 rem(10px);
    text-align: center;
    line-height: 1.2;
    border-radius: $radius-md;
    background-color: $color-secondary-gray;
    margin-bottom: rem(27px); }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, minmax(rem(100px), rem(272px)));
    gap: rem(13px) rem(10px);
    margin-top: rem(0); }
  &__listItem {
    aspect-ratio: 272/182;
    width: 100%;
    position: relative;
    border-radius: $radius-md;
    overflow: hidden;
    cursor: pointer;

    &__wrapper {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 80%;
      border-radius: $radius-md;
      background-color: $color-white;
      aspect-ratio: 172/60;
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 5;
      img {
        width: 75%;
        position: unset;
        transform: unset; } }

    &:after {
        position: absolute;
        content: '';
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: $color-red;
        z-index: 2;
        opacity: 0;
        transform: scale(0.9); }

    @include can-hover {
      &:hover {
        &:after {
          opacity: 1;
          transform: scale(1); } } }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.4);
      pointer-events: none; } }

  &__listItem__bg {
    width: 100%;
    height: 100%;
    object-fit: cover; }

  &__listItem__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 60%;
    aspect-ratio: 163/41; }

  &__listItem__name {} }


@include max-screen($breakpoint-lg) {
  .manfacturers {
    padding: rem(42px) 0 rem(29px);
    &__listItem {
      &__wrapper {
        border-radius: $radius-1;
        width: 60%; }
      &__list {
        grid-template-columns: repeat(3, minmax(rem(100px), rem(372px))); } } } }

@include max-screen($breakpoint-md) {
  .manfacturers {
    &__list {
      grid-template-columns: repeat(2, minmax(rem(100px), rem(372px))); } } }

@include max-screen($breakpoint-sm) {
  .manfacturers {
    &__list {
      grid-template-columns: repeat(1, minmax(rem(100px), rem(650px))); }
    &__listItem {
      aspect-ratio: 3/2; } } }

