.form {
	input {
		max-width: 100%; } }
form.wpcf7-form {
	input:not(input[type="file"]),
	select,
	textarea {
		padding: 14px 20px 13px;
		border: 1px solid #000;
		border-radius: 100px;
		font-size: 15px;
		line-height: 1.33333;
		width: 100%;
		@include max-screen($breakpoint-lg) {
			padding: 10px 16px 10px;
			font-size: 14px; }
		@include max-screen($breakpoint-md) {
			padding: 8px 13px 7px;
			font-size: 12px; }
		&::placeholder {
			color: #000; } }
	select {
		padding-top: 14.49px;
		padding-bottom: 13.49px;
		-o-appearance: none;
		-ms-appearance: none;
		-webkit-appearance: none;
		-moz-appearance: none;
		appearance: none;
		padding-right: 28px;
		@include max-screen($breakpoint-md) {
			padding-top: 8.49px;
			padding-bottom: 7.49px;
			padding-right: 18px; } }
	textarea {
		padding: 20px;
		border-radius: 20px;
		height: 130px;
		resize: none; }
	.file-btn {
		background: transparent;
		padding: 8px 12px;
		border-radius: 20px;
		margin-right: 12px;
		margin-bottom: 12px;
		@include max-screen($breakpoint-md) {
			padding: 6px 9px;
			margin-right: 8px;
			font-size: 12px; }
		&#btn_file-3 {
			margin-bottom: 19px;
			@include max-screen($breakpoint-lg) {
				margin-bottom: 14px; }
			@include max-screen($breakpoint-md) {
                margin-bottom: 8px; } } }
	.file-btn-label {
		font-size: 12px; }
	.file-btn-label + span {
		opacity: 0;
		width: 0;
		height: 0;
		display: block;
		visibility: hidden;
		margin: 0; }
	.wpcf7-form-control-wrap {
		margin-bottom: 19px;
		display: block;
		@include max-screen($breakpoint-lg) {
			margin-bottom: 14px; }
		@include max-screen($breakpoint-md) {
            margin-bottom: 8px; } }
	.form-field:nth-last-child(2) {
		& > p {
			display: flex;
			flex-direction: row-reverse;
			justify-content: flex-start;
			align-items: center;
			input {
				margin: 0; } } }
	.confirmation-error {
		font-size: 12px;
		margin-top: 4px;
		padding-left: 20px;
		color: #dc3232;
		display: block; }
	.select {
		.wpcf7-form-control-wrap {
			position: relative;
			&::before {
				content: "";
				position: absolute;
				top: 50%;
				width: 10px;
				height: 12px;
				right: 12px;
				transform: rotate(90deg) translateX(-60%);
				background-image: url('../assets/icons/right-arrow.png');
				background-size: contain;
				background-repeat: no-repeat;
				background-position: center;
				@include max-screen($breakpoint-md) {
					width: 7px;
					height: 9px;
					right: 10px;
					transform: rotate(90deg) translateX(-70%); } } } } }
