.productBlockHeading {
  margin: rem(20px) 0;
  &__wrapper {
    background-color: $color-red;
    border-radius: $radius-md;
    padding: rem(17px); }

  h3 {
    color: $color-white;
    text-align: center; } }

@include max-screen($breakpoint-sm) {
  .productBlockHeading {
    &__wrapper {
      padding: rem(14px); }

    h3 {
      font-size: rem(16px); } } }
