.brands {
  padding: rem(120px) 0 rem(154px);

  .manfacturers__listItem {
    position: relative;
    &__wrapper {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 80%;
      border-radius: $radius-md;
      background-color: $color-white;
      aspect-ratio: 172/60;
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 5;
      img {
        width: 75%;
        position: unset;
        transform: unset; } }

    &:after {
      position: absolute;
      content: '';
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: $color-red;
      z-index: 2;
      opacity: 0;
      transform: scale(0.9); }

    @include can-hover {
      &:hover {
        &:after {
          opacity: 1;
          transform: scale(1); } } } }


  .manfacturers__listItem__logo {
    z-index: 3; } }

@include max-screen($breakpoint-lg) {
  .brands {
    padding: rem(42px) 0 rem(29px);
    .manfacturers__listItem {
      &__wrapper {
        border-radius: $radius-1;
        width: 60%; } } } }
